import * as React from 'react';
import {Link, IndexLink} from 'react-router';
import {RoutePath} from '../../commons/constant';

export class MyProfile extends React.Component<any,any> {
  render() {
    return (
      <div className="main-container">
        <div className="container">
          <div className="row">
            <div className="col-sm-3 page-sidebar">
              <aside>
                <div className="inner-box">
                  <div className="user-panel-sidebar">
                    <div className="collapse-box">
                      <h5 className="collapse-title no-border">TRANG CÁ NHÂN<a href="#MyclassNameified"
                                                                               data-toggle="collapse"
                                                                               className="pull-right"><i
                        className="fa fa-angle-down"/></a></h5>
                      <div className="panel-collapse collapse in" id="MyclassNameified">
                        <ul className="acc-list">
                          <li><IndexLink activeClassName="active" to={RoutePath.MyProfile}><i className="icon-home"/>
                            Thông Tin Cá Nhân </IndexLink></li>
                          <li><Link to={RoutePath.MyProfile_Settings} activeClassName="active">
                            <i className="icon-lock"/>
                            Tài Khoản &amp; Bảo Mật </Link></li>
                          <li><Link to={RoutePath.MyProfile_Mailbox} activeClassName="active">
                            <i className="icon-mail"/>Hòm thư <span className="badge">7</span>
                          </Link></li>
                        </ul>
                      </div>
                    </div>
                    <div className="collapse-box">
                      <h5 className="collapse-title"> TIN ĐĂNG CỦA TÔI <a href="#MyAds" data-toggle="collapse" className="pull-right">
                        <i className="fa fa-angle-down"/></a></h5>
                      <div className="panel-collapse collapse in" id="MyAds">
                        <ul className="acc-list">
                          <li><Link to={RoutePath.MyProfile_Ads} activeClassName="active">
                            <i className="icon-doc-text-inv"/>Tin Đang Hoạt Động <span className="badge">10</span>
                          </Link></li>
                          <li><Link to={RoutePath.MyProfile_Archive} activeClassName="active">
                            <i className="icon-folder-close"/>
                            Tin Lưu Trữ <span className="badge">20</span> </Link></li>
                        </ul>
                      </div>
                    </div>
                    <div className="collapse-box">
                      <h5 className="collapse-title"> XÓA TÀI KHOẢN <a href="#TerminateAccount" data-toggle="collapse"
                                                                       className="pull-right"><i
                        className="fa fa-angle-down"/></a></h5>
                      <div className="panel-collapse collapse in" id="TerminateAccount">
                        <ul className="acc-list">
                          <li><a href="#"><i className="icon-cancel-circled "/> Xóa Tài Khoản </a></li>
                        </ul>
                      </div>
                    </div>
                  </div>
                </div>
              </aside>
            </div>
            <div className="col-sm-9 page-content">
              <div className="inner-box">
                <div className="row">
                  <div className="col-md-4 col-xs-4 col-xxs-6 avatar-profile">
                    <div className="avatar-img">
                      <a href=""><img className="userImg"
                                      src="http://orig14.deviantart.net/1eeb/f/2010/030/c/0/avatar_girl_by_stefibn.jpg"
                                      alt="user"/></a>
                    </div>
                    <div className="avatar-pro">
                      <span className="profile-name">
                        HT Active Long Long Long Name
                      </span>
                      <span className="profile-name-rate">
                        <b>1000</b> Đánh Giá
                      </span>
                      <span className="profile-name-star">
                        4 / 5 <i className="fa fa-star"/>
                      </span>
                      <span className="profile-verify">
                        <span title="Đã được xác thực số điện thoại"><i className="fa fa-mobile mobile-verify"/></span>

                        <span title="Tài khoản tin cậy"><i className="fa fa-shield account-verify"/></span>
                      </span>
                    </div>
                  </div>
                  <div className="col-md-3 col-xs-3 col-xxs-6 no-padding rate-text">
                    <strong>300</strong> <span>Hài Lòng  </span><br />
                    <strong>300</strong> <span>Bình Thường  </span><br />
                    <strong>400</strong> <span>Không Hài Lòng </span>

                  </div>
                  <div className="col-md-5 col-xs-5 col-xxs-12">
                    <div className="header-data text-center-xs">
                      <div className="hdata" style={{width: 110}}>
                        <div className="mcol-left">
                          <i className="icon-th-thumb ln-shadow"/></div>
                        <div className="mcol-right">
                          <p><a href="#">50</a><em>Bài Đăng</em></p>
                        </div>
                        <div className="clearfix"></div>
                      </div>
                      <div className="hdata" style={{width: 110}}>
                        <div className="mcol-left">
                          <i className="icon-folder-close ln-shadow"/></div>
                        <div className="mcol-right">
                          <p><a href="#">20</a> <em>Lưu Trữ</em></p>
                        </div>
                        <div className="clearfix"></div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
              {this.props.children}
            </div>
          </div>
        </div>
      </div>
    );
  }
}


