import * as React from 'react';


export class ContactPage extends React.Component<any,any> {
  render() {
    return (
      <div>

        <div id="wrapper">
          <div className="intro-inner">
            <div className="contact-intro">
              <div className="w100 map">
                <iframe
                  src="https://www.google.com/maps/embed?pb=!1m14!1m12!1m3!1d26081603.294420466!2d-95.677068!3d37.06250000000001!2m3!1f0!2f0!3f0!3m2!1i1024!2i768!4f13.1!5e0!3m2!1sen!2s!4v1423809000824"
                  width="100%" height="350" frameBorder="0" style={{border:0}}></iframe>
              </div>
            </div>

          </div>

          <div className="main-container">
            <div className="container">
              <div className="row clearfix">
                <div className="col-md-4">
                  <div className="contact_info">
                    <h5 className="list-title gray"><strong>Contact us</strong></h5>
                    <div className="contact-info ">
                      <div className="address">
                        <p className="p1">220 Fifth Ave</p>
                        <p className="p1">2nd Flr. New York, NY 10001 </p>
                        <p>Email: info@@demos.org</p>
                        <p>Toll Free: 212-633-1405</p>
                        <p>&nbsp;</p>
                        <div>
                          <p><strong><a href="#">Get a Quote</a></strong></p>
                          <p><strong> <a href="login.html">Client Area Login</a></strong></p>
                          <p><strong> <a href="#skypeid" className="skype">Live Chat</a></strong></p>
                          <p><strong> <a href="faq.html">Knowledge Base</a></strong></p>
                        </div>
                      </div>
                    </div>
                    <div className="social-list"><a target="_blank" href="https://twitter.com/"><i
                      className="fa fa-twitter fa-lg "></i></a>
                      <a target="_blank" href="https://www.facebook.com/"><i className="fa fa-facebook fa-lg "></i></a>
                      <a target="_blank" href="https://plus.google.com"><i className="fa fa-google-plus fa-lg "></i></a>
                      <a target="_blank" href="https://www.pinterest.com/"><i className="fa fa-pinterest fa-lg "></i></a>
                    </div>
                  </div>
                </div>
                <div className="col-md-8">
                  <div className="contact-form">
                    <h5 className="list-title gray"><strong>Contact us</strong></h5>
                    <form className="form-horizontal" method="post">
                      <fieldset>
                        <div className="row">
                          <div className="col-sm-6">
                            <div className="form-group">
                              <div className="col-md-12">
                                <input id="firstname" name="name" type="text" placeholder="First Name"
                                       className="form-control"/>
                              </div>
                            </div>
                          </div>
                          <div className="col-sm-6">
                            <div className="form-group">
                              <div className="col-md-12">
                                <input id="lastname" name="name" type="text" placeholder="Last  Name"
                                       className="form-control"/>
                              </div>
                            </div>
                          </div>
                          <div className="col-sm-6">
                            <div className="form-group">
                              <div className="col-md-12">
                                <input id="companyname" name="name" type="text" placeholder="Company Name"
                                       className="form-control"/>
                              </div>
                            </div>
                          </div>
                          <div className="col-sm-6">
                            <div className="form-group">
                              <div className="col-md-12">
                                <input id="email" name="email" type="text" placeholder="Email Address"
                                       className="form-control"/>
                              </div>
                            </div>
                          </div>
                          <div className="col-lg-12">
                            <div className="form-group">
                              <div className="col-md-12">
                                <textarea className="form-control" id="message" name="message"
                                          placeholder="Enter your massage for us here. We will get back to you within 2 business days."
                                          rows={7}></textarea>
                              </div>
                            </div>
                            <div className="form-group">
                              <div className="col-md-12 ">
                                <button type="submit" className="btn btn-primary btn-lg">Submit</button>
                              </div>
                            </div>
                          </div>
                        </div>
                      </fieldset>
                    </form>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    )
  };
}



