import * as React from 'react';
export class MyProfileSettingsComponent extends React.Component<any, any> {
  render() {
    return (
      <div className="inner-box">
        <h2 className="title-2"><i className="icon-lock"></i> Cài đặt tài khoản </h2>
        <div className="panel panel-default">
          <div className="panel-heading">
            <h4 className="panel-title"><a href="#collapseB2" data-toggle="collapse" className="" aria-expanded="true">
              Cài đặt </a>
            </h4>
          </div>
          <div className="panel-collapse collapse in" id="collapseB2" aria-expanded="true">
            <div className="panel-body">
              <form className="form-horizontal" role="form">
                <div className="form-group">
                  <div className="col-sm-12">
                    <div className="checkbox">
                      <label>
                        <input type="checkbox"/>
                        Nhận tin nhắn từ ban quản trị </label>
                    </div>
                  </div>
                </div>
                <div className="form-group">
                  <div className="col-sm-12">
                    <div className="checkbox">
                      <label>
                        <input type="checkbox"/>
                        Nhận tin nhắn từ thành viên khác </label>
                    </div>
                  </div>
                </div>
                <div className="form-group">
                  <label className="col-sm-3 control-label">Mật khẩu mới</label>
                  <div className="col-sm-9">
                    <input type="password" className="form-control" placeholder=""/>
                  </div>
                </div>
                <div className="form-group">
                  <label className="col-sm-3 control-label">Xác nhận mật khẩu mới</label>
                  <div className="col-sm-9">
                    <input type="password" className="form-control" placeholder=""/>
                  </div>
                </div>
                <div className="form-group">
                  <div className="col-sm-offset-3 col-sm-9">
                    <button type="submit" className="btn btn-default">Đổi mật khẩu</button>
                  </div>
                </div>
              </form>
            </div>
          </div>
        </div>
        <div id="accordion" className="panel-group">


          <div className="panel panel-default">
            <div className="panel-heading">
              <h4 className="panel-title"><a href="#collapseB3" data-toggle="collapse" className=""
                                             aria-expanded="true">
                Thông báo </a></h4>
            </div>
            <div className="panel-collapse collapse in" id="collapseB3" aria-expanded="true">
              <div className="panel-body">
                <div className="form-group">
                  <div className="col-sm-12">
                    <div className="checkbox">
                      <label>
                        <input type="checkbox"/>
                        Tôi muốn nhận mail thông báo tin tức. </label>
                    </div>
                    <div className="checkbox">
                      <label>
                        <input type="checkbox"/>
                        Tôi muốn nhận mail quảng cáo. </label>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>

        </div>
      </div>
    );
  }
}


