import * as React from 'react';
import {Header} from './components/header'
import {Footer} from './components/footer'
export class Index extends React.Component<any,any> {
  render() {
    return (
      <div>
        <Header/>
        {this.props.children}
        <Footer />
      </div>
    );
  }
}


