import * as React from 'react';
export class PostAdPage extends React.Component<any,any>{
  render(){
    return (
      <div className="main-container">
        <div className="container">
          <div className="row">
            <div className="col-md-9 page-content">
              <div className="inner-box category-content">
                <h2 className="title-2 uppercase"><strong> <i className="icon-docs"></i> Post a Free Classified
                  Ad</strong></h2>
                <div className="row">
                  <div className="col-sm-12">
                    <form className="form-horizontal">
                      <fieldset>

                        <div className="form-group">
                          <label className="col-md-3 control-label">Category</label>
                          <div className="col-md-8">
                            <select name="category-group" id="category-group" className="form-control">
                              <option value="0" defaultValue="selected"> Select a category...</option>
                              <option value="Vehicles" style={{backgroundColor:'#E9E9E9',fontWeight:'bold'}} disabled> - Vehicles -
                              </option>
                              <option value="Cars"> Cars</option>
                              <option value="Commercial vehicles"> Commercial vehicles</option>
                              <option value="Motorcycles"> Motorcycles</option>
                              <option value="Motorcycle Equipment"> Car &amp; Motorcycle
                                Equipment
                              </option>
                              <option value="Boats"> Boats</option>
                              <option value="Vehicles"> Other Vehicles</option>
                              <option value="House" style={{backgroundColor:'#E9E9E9',fontWeight:'bold'}} disabled> - House and Children -
                              </option>
                              <option value="Appliances"> Appliances</option>
                              <option value="Inside"> Inside</option>
                              <option value="Games"> Games and Clothing</option>
                              <option value="Garden"> Garden</option>
                              <option value="Multimedia" style={{backgroundColor:'#E9E9E9',fontWeight:'bold'}}  disabled> - Multimedia -
                              </option>
                              <option value="Telephony"> Telephony</option>
                              <option value="Image"> Image and sound</option>
                              <option value="Computers"> Computers and Accessories</option>
                              <option value="Video"> Video games and consoles</option>
                              <option value="Real" style={{backgroundColor:'#E9E9E9',fontWeight:'bold'}}  disabled> - Real Estate -
                              </option>
                              <option value="Apartment"> Apartment</option>
                              <option value="Home"> Home</option>
                              <option value="Vacation"> Vacation Rentals</option>
                              <option value="Commercial"> Commercial offices and local</option>
                              <option value="Grounds"> Grounds</option>
                              <option value="Houseshares"> Houseshares</option>
                              <option value="Other real estate"> Other real estate</option>
                              <option value="Services" style={{backgroundColor:'#E9E9E9',fontWeight:'bold'}}  disabled> - Services -
                              </option>
                              <option value="Jobs"> Jobs</option>
                              <option value="Job application"> Job application</option>
                              <option value="Services"> Services</option>
                              <option value="Price"> Price</option>
                              <option value="Business"> Business and goodwill</option>
                              <option value="Professional"> Professional equipment</option>
                              <option value="dropoff" style={{backgroundColor:'#E9E9E9',fontWeight:'bold'}} disabled> - Extra -
                              </option>
                              <option value="Other"> Other</option>
                            </select>
                          </div>
                        </div>

                        <div className="form-group">
                          <label className="col-md-3 control-label">Add Type</label>
                          <div className="col-md-8">
                            <label className="radio-inline" htmlFor="radios-0">
                              <input name="radios" id="radios-0" value="Private" defaultChecked type="radio"/>
                                Private </label>
                            <label className="radio-inline" htmlFor="radios-1">
                              <input name="radios" id="radios-1" defaultValue="Business" type="radio"/>
                                Business </label>
                          </div>
                        </div>

                        <div className="form-group">
                          <label className="col-md-3 control-label" htmlFor="Adtitle">Ad title</label>
                          <div className="col-md-8">
                            <input id="Adtitle" name="Adtitle" placeholder="Ad title" className="form-control input-md" required type="text"/>
                              <span className="help-block">A great title needs at least 60 characters. </span>
                          </div>
                        </div>

                        <div className="form-group">
                          <label className="col-md-3 control-label" htmlFor="textarea">Describe ad </label>
                          <div className="col-md-8">
<textarea className="form-control" id="textarea" name="textarea" placeholder="Describe what makes your ad unique" ></textarea>
                          </div>
                        </div>

                        <div className="form-group">
                          <label className="col-md-3 control-label" htmlFor="Price">Price</label>
                          <div className="col-md-4">
                            <div className="input-group"><span className="input-group-addon">$</span>
                              <input id="Price" name="Price" className="form-control" placeholder="placeholder" required type="text"/>
                            </div>
                          </div>
                          <div className="col-md-4">
                            <div className="checkbox">
                              <label>
                                <input type="checkbox"/>
                                  Negotiable </label>
                            </div>
                          </div>
                        </div>

                        <div className="form-group">
                          <label className="col-md-3 control-label" htmlFor="textarea"> Picture </label>
                          <div className="col-md-8">
                            <div className="mb10">
                              <input id="input-upload-img1" type="file" className="file" data-preview-file-type="text"/>
                            </div>
                            <div className="mb10">
                              <input id="input-upload-img2" type="file" className="file" data-preview-file-type="text"/>
                            </div>
                            <div className="mb10">
                              <input id="input-upload-img3" type="file" className="file" data-preview-file-type="text"/>
                            </div>
                            <div className="mb10">
                              <input id="input-upload-img4" type="file" className="file" data-preview-file-type="text"/>
                            </div>
                            <div className="mb10">
                              <input id="input-upload-img5" type="file" className="file" data-preview-file-type="text"/>
                            </div>
                            <p className="help-block">Add up to 5 photos. Use a real image of your
                              product, not catalogs.</p>
                          </div>
                        </div>
                        <div className="content-subheading"><i className="icon-user fa"></i> <strong>Seller
                          information</strong></div>

                        <div className="form-group">
                          <label className="col-md-3 control-label" htmlFor="textinput-name">Name</label>
                          <div className="col-md-8">
                            <input id="textinput-name" name="textinput-name" placeholder="Seller Name" className="form-control input-md" required type="text"/>
                          </div>
                        </div>

                        <div className="form-group">
                          <label className="col-md-3 control-label" htmlFor="seller-email"> Seller
                            Email</label>
                          <div className="col-md-8">
                            <input id="seller-email" name="seller-email" className="form-control" placeholder="Email" required type="text"/>
                              <div className="checkbox">
                                <label>
                                  <input type="checkbox" defaultValue=""/>
                                    <small> Hide the phone number on this ads.</small>
                                </label>
                              </div>
                          </div>
                        </div>

                        <div className="form-group">
                          <label className="col-md-3 control-label" htmlFor="seller-Number">Phone
                            Number</label>
                          <div className="col-md-8">
                            <input id="seller-Number" name="seller-Number" placeholder="Phone Number" className="form-control input-md" required type="text"/>
                          </div>
                        </div>

                        <div className="form-group">
                          <label className="col-md-3 control-label" htmlFor="seller-Location">Location</label>
                          <div className="col-md-8">
                            <select id="seller-Location" name="seller-Location" className="form-control">
                              <option value="1">Option one</option>
                              <option value="2">Option two</option>
                            </select>
                          </div>
                        </div>

                        <div className="form-group">
                          <label className="col-md-3 control-label" htmlFor="seller-area">City</label>
                          <div className="col-md-8">
                            <select id="seller-area" name="seller-area" className="form-control">
                              <option value="1">Option one</option>
                              <option value="2">Option two</option>
                            </select>
                          </div>
                        </div>
                        <div className="well">
                          <h3><i className=" icon-certificate icon-color-1"></i> Make your Ad Premium
                          </h3>
                          <p>Premium ads help sellers promote their product or service by getting
                            their ads more visibility with more
                            buyers and sell what they want faster. <a href="help.html">Learn
                              more</a></p>
                          <div className="form-group">
                            <table className="table table-hover checkboxtable">
                            <tbody>
                              <tr>
                                <td>
                                  <div className="radio">
                                    <label>
                                      <input type="radio" name="optionsRadios" id="optionsRadios0" value="option0" defaultChecked/>
                                        <strong>Regular List </strong> </label>
                                  </div>
                                </td>
                                <td><p>$00.00</p></td>
                              </tr>
                              <tr>
                                <td>
                                  <div className="radio">
                                    <label>
                                      <input type="radio" name="optionsRadios" id="optionsRadios1" value="option1"/>
                                        <strong>Urgent Ad </strong> </label>
                                  </div>
                                </td>
                                <td><p>$10.00</p></td>
                              </tr>
                              <tr>
                                <td>
                                  <div className="radio">
                                    <label>
                                      <input type="radio" name="optionsRadios" id="optionsRadios2" value="option2"/>
                                        <strong>Top of the Page Ad </strong> </label>
                                  </div>
                                </td>
                                <td><p>$20.00</p></td>
                              </tr>
                              <tr>
                                <td>
                                  <div className="radio">
                                    <label>
                                      <input type="radio" name="optionsRadios" id="optionsRadios3" value="option3"/>
                                        <strong>Top of the Page Ad + Urgent Ad </strong>
                                    </label>
                                  </div>
                                </td>
                                <td><p>$40.00</p></td>
                              </tr>
                              <tr>
                                <td>
                                  <div className="form-group">
                                    <div className="col-md-8">
                                      <select className="form-control" name="Method" id="PaymentMethod">
                                        <option value="2">Select Payment Method</option>
                                        <option value="3">Credit / Debit Card</option>
                                        <option value="5">Paypal</option>
                                      </select>
                                    </div>
                                  </div>
                                </td>
                                <td><p><strong>Payable Amount : $40.00</strong></p></td>
                              </tr>
                            </tbody>
                            </table>
                          </div>
                        </div>

                        <div className="form-group">
                          <label className="col-md-3 control-label">Terms</label>
                          <div className="col-md-8">
                            <label className="checkbox-inline" htmlFor="checkboxes-0">
                              <input name="checkboxes" id="checkboxes-0" value="Remember above contact information." type="checkbox"/>
                                Remember above contact information. </label>
                          </div>
                        </div>

                        <div className="form-group">
                          <label className="col-md-3 control-label"></label>
                          <div className="col-md-8"><a href="posting-success.html" id="button1id" className="btn btn-success btn-lg">Submit</a></div>
                        </div>
                      </fieldset>
                    </form>
                  </div>
                </div>
              </div>
            </div>

            <div className="col-md-3 reg-sidebar">
              <div className="reg-sidebar-inner text-center">
                <div className="promo-text-box"><i className=" icon-picture fa fa-4x icon-color-1"></i>
                  <h3><strong>Post a Free Classified</strong></h3>
                  <p> Post your free online classified ads with us. Lorem ipsum dolor sit amet, consectetur
                    adipiscing elit. </p>
                </div>
                <div className="panel sidebar-panel">
                  <div className="panel-heading uppercase">
                    <small><strong>How to sell quickly?</strong></small>
                  </div>
                  <div className="panel-content">
                    <div className="panel-body text-left">
                      <ul className="list-check">
                        <li> Use a brief title and description of the item</li>
                        <li> Make sure you post in the correct category</li>
                        <li> Add nice photos to your ad</li>
                        <li> Put a reasonable price</li>
                        <li> Check the item before publish</li>
                      </ul>
                    </div>
                  </div>
                </div>
              </div>
            </div>

          </div>

        </div>

      </div>
  );
  }
}


