import * as React from 'react';
export class MyProfileInformationComponent extends React.Component<any, any>{
  render(){
    return (
      <div className="inner-box">
        <div className="welcome-msg">
          <h3 className="page-sub-header2 clearfix no-padding">Xin Chào HT Active </h3>
          <span className="page-sub-header-sub small">Đăng nhập lần cuối: 15 phút trước</span>
        </div>
        <div className="panel panel-default">
          <div className="panel-heading">
            <h4 className="panel-title"><a href="#collapseB1" data-toggle="collapse"> Thông tin cá nhân </a>
            </h4>
          </div>
          <div className="panel-collapse collapse in" id="collapseB1">
            <div className="panel-body">
              <form className="form-horizontal" role="form">
                <div className="form-group">
                  <label className="col-sm-3 control-label">Họ</label>
                  <div className="col-sm-9">
                    <input type="text" className="form-control" placeholder="HT"/>
                  </div>
                </div>
                <div className="form-group">
                  <label className="col-sm-3 control-label">Tên</label>
                  <div className="col-sm-9">
                    <input type="text" className="form-control" placeholder="Active"/>
                  </div>
                </div>
                <div className="form-group">
                  <label className="col-sm-3 control-label">Email</label>
                  <div className="col-sm-9">
                    <input type="email" className="form-control" placeholder="htactive@gmail.com"/>
                  </div>
                </div>
                <div className="form-group">
                  <label htmlFor="City" className="col-sm-3 control-label">Tỉnh/Thành phố</label>
                  <div className="col-sm-9">
                    <select className="form-control" id="City">
                      <option>---Chọn---</option>
                      <option>Hà Nội</option>
                      <option>Đà Nẵng</option>
                      <option>Sài Gòn</option>
                      <option>Cần Thơ</option>
                    </select>
                  </div>
                </div>
                <div className="form-group">
                  <label htmlFor="City" className="col-sm-3 control-label">Quận/Huyện</label>
                  <div className="col-sm-9">
                    <select className="form-control" id="City">
                      <option>---Chọn---</option>
                      <option>Sơn Trà</option>
                      <option>Ngũ Hành Sơn</option>
                      <option>Thanh Khê</option>
                    </select>
                  </div>
                </div>
                <div className="form-group">
                  <label className="col-sm-3 control-label">Địa chỉ</label>
                  <div className="col-sm-9">
                    <textarea rows={4} type="text" className="form-control" placeholder="Số nhà, đường..."/>
                  </div>
                </div>
                <div className="form-group hide">
                  <label className="col-sm-3 control-label">Liên kết đến Facebook</label>
                  <div className="col-sm-9">
                    <div className="form-control"><a className="link" href="fb.com">Htactive</a> <a
                      className=""> <i className="fa fa-minus-circle"/></a></div>
                  </div>
                </div>
                <div className="form-group">
                  <div className="col-sm-offset-3 col-sm-9"></div>
                </div>
                <div className="form-group">
                  <div className="col-sm-offset-3 col-sm-9">
                    <button type="submit" className="btn btn-default">Cập Nhật</button>
                  </div>
                </div>
              </form>
            </div>
          </div>
        </div>
      </div>);
  }
}


