import * as React from 'react';
export class SignupPage extends React.Component<any,any>{
  render(){
    return (

      <div className="main-container">
        <div className="container">
          <div className="row">
            <div className="col-md-8 page-content">
              <div className="inner-box category-content">
                <h2 className="title-2"><i className="icon-user-add"></i> Đăng ký tài khoản </h2>
                <div className="row">
                  <div className="col-sm-12">
                    <form className="form-horizontal">
                      <fieldset>
                        <div className="form-group required">
                          <label className="col-md-4 control-label">Bạn là <sup>*</sup></label>
                          <div className="col-md-6">
                            <div className="radio">
                              <label>
                                <input type="radio" name="optionsRadios" id="optionsRadios1" defaultValue="option1" defaultChecked />
                                  Cá nhân </label>
                            </div>
                            <div className="radio">
                              <label>
                                <input type="radio" name="optionsRadios" id="optionsRadios2" defaultValue="option2"/>
                                  Công ty </label>
                            </div>
                          </div>
                        </div>

                        <div className="form-group required">
                          <label className="col-md-4 control-label">Tên <sup>*</sup></label>
                          <div className="col-md-6">
                            <input name="" placeholder="Tên" className="form-control input-md" required type="text"/>
                          </div>
                        </div>

                        <div className="form-group required">
                          <label className="col-md-4 control-label">Họ <sup>*</sup></label>
                          <div className="col-md-6">
                            <input name="textinput" placeholder="Họ" className="form-control input-md" type="text"/>
                          </div>
                        </div>

                        <div className="form-group required">
                          <label className="col-md-4 control-label">Số điện thoại <sup>*</sup></label>
                          <div className="col-md-6">
                            <input name="textinput" placeholder="Số điện thoại" className="form-control input-md" type="text"/>
                              {/*<div className="checkbox">*/}
                                {/*<label>*/}
                                  {/*<input type="checkbox" defaultValue=""/>*/}
                                    {/*<small> Hide the phone number on the published ads.</small>*/}
                                {/*</label>*/}
                              {/*</div>*/}
                          </div>
                        </div>

                        <div className="form-group">
                          <label className="col-md-4 control-label">Giới tính</label>
                          <div className="col-md-6">
                            <div className="radio">
                              <label htmlFor="Gender-0">
                                <input name="Gender" id="Gender-0" defaultValue="1" defaultChecked type="radio"/>
                                  Nam </label>
                            </div>
                            <div className="radio">
                              <label htmlFor="Gender-1">
                                <input name="Gender" id="Gender-1" defaultValue="2" type="radio"/>
                                  Nữ </label>
                            </div>
                          </div>
                        </div>

                        <div className="form-group">
                          <label className="col-md-4 control-label" htmlFor="textarea">Giới thiệu về bạn</label>
                          <div className="col-md-6">
<textarea className="form-control"
          placeholder="Giới thiệu về bạn" id="textarea" name="textarea"/>

                          </div>
                        </div>
                        <div className="form-group required">
                          <label htmlFor="inputEmail3" className="col-md-4 control-label">Email
                            <sup>*</sup></label>
                          <div className="col-md-6">
                            <input type="email" className="form-control" id="inputEmail3" placeholder="Email"/>
                          </div>
                        </div>
                        <div className="form-group required">
                          <label htmlFor="inputPassword3" className="col-md-4 control-label">Mật khẩu </label>
                          <div className="col-md-6">
                            <input type="password" className="form-control" id="inputPassword3" placeholder="Nhập mật khẩu"/>
                              <p className="help-block">Ít nhất 5 ký tự
                              </p>
                          </div>
                        </div>
                        <div className="form-group">
                          <label className="col-md-4 control-label"></label>
                          <div className="col-md-8">
                            <div className="termbox mb10">
                              <label className="checkbox-inline" htmlFor="checkboxes-1">
                                <input name="checkboxes" id="checkboxes-1" defaultValue="1" type="checkbox"/>
                                  Tôi đã đọc và đồng ý với <a href="terms-conditions.html">điều khoản &amp; điều kiện</a> </label>
                            </div>
                            <div style={{clear:'both'}}></div>
                            <a className="btn btn-primary" href="account-home.html">Đăng ký</a></div>
                        </div>
                      </fieldset>
                    </form>
                  </div>
                </div>
              </div>
            </div>

            <div className="col-md-4 reg-sidebar">
              <div className="reg-sidebar-inner text-center">
                <div className="promo-text-box"><i className=" icon-picture fa fa-4x icon-color-1"></i>
                  <h3><strong>Đăng tin rao vặt miễn phí</strong></h3>
                  <p> Đăng tin trực tuyến miễn phí với chúng tôi. </p>
                </div>
                <div className="promo-text-box"><i className=" icon-pencil-circled fa fa-4x icon-color-2"></i>
                  <h3><strong>Tạo và quản lý các tin rao vặt</strong></h3>
                  <p> Tạo mới và quản lý các tin đã được đăng. </p>
                </div>
                <div className="promo-text-box"><i className="  icon-heart-2 fa fa-4x icon-color-3"></i>
                  <h3><strong>Tạo danh sách quảng cáo yêu thích</strong></h3>
                  <p> Tạo và quản lý danh sách quảng cáo yêu thích của bạn. </p>
                </div>
              </div>
            </div>
          </div>

        </div>

      </div>

  );
  }
}


