import * as React from 'react';
import * as ReactDOM from 'react-dom';
import {Router, browserHistory, Route, IndexRoute} from 'react-router';
import {RoutePath} from './commons/constant';
import {Error404Page} from './app/modules/404-page';
import {Index} from './app/index';
import {HomePage} from './app/modules/home-page';
import {ProductPage} from './app/modules/product-page';
import {ProductDetailPage} from './app/modules/product-detail-page';
import {PostAdPage} from './app/modules/postad-page';
import {LoginPage} from './app/modules/login-page';
import {SignupPage} from './app/modules/signup-page';
import {TermPage} from './app/modules/term-page';
import {FAQPage} from './app/modules/faq-page';
import {JobListPage} from './app/modules/joblist-page';
import {ContactPage} from './app/modules/contacts-page';
import {PropertyListPage} from './app/modules/propertylist-page';
import {AboutUsPage} from './app/modules/about-page';
import {MyProfile} from './app/modules/my-profile';
import {MyProfileInformationComponent} from './app/components/my-profile/information';
import {MyProfileSettingsComponent} from './app/components/my-profile/settings';
import {MyProfileAdsComponent} from './app/components/my-profile/my-ads';
import {MyProfileArchiveComponent} from './app/components/my-profile/my-archive';
import {MailboxComponent} from './app/components/my-profile/mailbox';

ReactDOM.render(
  <Router history={browserHistory}>
    <Route path={RoutePath.Error404} component={Error404Page}/>
    <Route path={RoutePath.Index} component={Index}>
      <Route path={RoutePath.About} component={AboutUsPage}/>
      <Route path={RoutePath.Contact} component={ContactPage}/>
      <Route path={RoutePath.TermConditions} component={TermPage}/>
      <Route path={RoutePath.Product} component={ProductPage}/>
      <Route path={RoutePath.ProductDetail} component={ProductDetailPage}/>
      <Route path={RoutePath.Post} component={PostAdPage}/>
      <Route path={RoutePath.Login} component={LoginPage}/>
      <Route path={RoutePath.SignUp} component={SignupPage}/>
      <Route path={RoutePath.FAQ} component={FAQPage}/>
      <Route path={RoutePath.JobList} component={JobListPage}/>
      <Route path={RoutePath.PropertyList} component={PropertyListPage}/>
      <Route path={RoutePath.MyProfile} component={MyProfile}>
        <Route path={RoutePath.MyProfile_Settings} component={MyProfileSettingsComponent}/>
        <Route path={RoutePath.MyProfile_Ads} component={MyProfileAdsComponent}/>
        <Route path={RoutePath.MyProfile_Archive} component={MyProfileArchiveComponent}/>
        <Route path={RoutePath.MyProfile_Mailbox} component={MailboxComponent}/>
        <IndexRoute component={MyProfileInformationComponent}/>
      </Route>
      <IndexRoute component={HomePage}/>
    </Route>
  </Router>,
  document.getElementById('root')
);


