import * as React from 'react';
export class LoginPage extends React.Component<any,any>{
  render(){
    return (
      <div className="main-container">
        <div className="container">
          <div className="row">
            <div className="col-sm-5 login-box">
              <div className="panel panel-default">
                <div className="panel-intro text-center">
                  <h2 className="logo-title">
                    <span className="logo-icon"><i className="icon icon-search-1 ln-shadow-logo shape-0"></i> </span> CHỢ<span> TRỜI</span>
                  </h2>
                </div>
                <div className="panel-body">
                  <form role="form">
                    <div className="form-group">
                      <label htmlFor="sender-email" className="control-label">Tài khoản:</label>
                      <div className="input-icon"><i className="icon-user fa"></i>
                        <input id="sender-email" type="text" placeholder="Tài khoản" className="form-control email"/>
                      </div>
                    </div>
                    <div className="form-group">
                      <label htmlFor="user-pass" className="control-label">Mật khẩu:</label>
                      <div className="input-icon"><i className="icon-lock fa"></i>
                        <input type="password" className="form-control" placeholder="Mật khẩu" id="user-pass"/>
                      </div>
                    </div>
                    <div className="form-group">
                      <a href="http://templatecycle.com/demo/bootclassified-v1.6/dist/account-home.html" className="btn btn-primary  btn-block">Submit</a>
                    </div>
                  </form>
                </div>
                <div className="panel-footer">
                  <div className="checkbox pull-left">
                    <label> <input type="checkbox" value="1" name="remember" id="remember"/> Ghi nhớ tài khoản</label>
                  </div>
                  <p className="text-center pull-right"><a href="http://templatecycle.com/demo/bootclassified-v1.6/dist/forgot-password.html"> Quên mật khẩu? </a>
                  </p>
                  <div style={{clear:'both'}}></div>
                </div>
              </div>
              <div className="login-box-btm text-center">
                <p> Chưa có tài khoản? <br/>
                  <a href="http://templatecycle.com/demo/bootclassified-v1.6/dist/signup.html"><strong>Đăng ký</strong> </a></p>
              </div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}


